/*
  Style for Archives
*/

%date-timeline {
    content: "";
    width: 4px;
    right: 75px;
    display: inline-block;
    float: right;
    position: relative;
    background-color: var(--timeline-color);
}

#archives {
    letter-spacing: 0.03rem;
    span.lead {
        font-size: 1.5rem;
        position: relative;
        right: 8px;
        &::after {
            /* Year dot */
            content: "";
            display: block;
            position: relative;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            width: 12px;
            height: 12px;
            top: -26px;
            right: 63px;
            border: 3px solid;
            background-color: var(--timeline-year-dot-color);
            border-color: var(--timeline-node-bg);
            box-shadow: 0 0 2px 0 #c2c6cc;
            z-index: 1;
        }
        &:not(:first-child) {
            position: relative;
            right: 4px;
            &::after {
                right: 67px;
            }
        }
    } // #archives span.lead
    ul {
        li {
            font-size: 1.1rem;
            line-height: 3rem;
            div {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                a {
                    /* post title in Archvies */
                    margin-right: 2.5rem;
                    position: relative;
                    top: 0.1rem;
                }
            }
            &:nth-child(odd) {
                background-color: var(--main-wrapper-bg, #fff);
                background-image: linear-gradient(to right, #fff, #fbfbfb, #fbfbfb, #fbfbfb, #fff);
            }
            &::after {
                @extend %date-timeline;
                height: 2.8rem;
                top: -1.3rem;
            }
            &:first-child::before {
                @extend %date-timeline;
                height: 3.06rem;
                top: -1.61rem;
            }
        }
        &:not(:last-child)>li:last-child::after {
            height: 3.4rem;
        }
        &:last-child>li:last-child::after {
            display: none;
        }
    } // #archives ul
    .date {
        white-space: nowrap;
        display: inline-block;
        &.month {
            width: 1.4rem;
            text-align: center;
            ~a::before {
                /* A dot for Month and Day */
                content: "";
                display: inline-block;
                position: relative;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                width: 8px;
                height: 8px;
                float: right;
                top: 1.35rem;
                right: 69px;
                background-color: var(--timeline-node-bg);
                box-shadow: 0 0 3px 0 #c2c6cc;
                z-index: 1;
            }
        }
        &.day {
            font-size: 85%;
            font-family: 'Lato', sans-serif;
            text-align: center;
            margin-left: -2px;
            width: 1.2rem;
            position: relative;
            right: -0.15rem;
        }
    } // #archives .date
} // #archives
@media all and (max-width: 576px) {
    #archives {
        margin-top: -1rem;
        ul {
            letter-spacing: 0;
        }
    }
}
