/*
  Style for Homepage
*/

.pagination {
    color: var(--btn-patinator-text-color);
    font-family: 'Lato', sans-serif;
    a:hover {
        text-decoration: none;
    }
    .page-item {
        .page-link {
            color: inherit;
            width: 2.5rem;
            height: 2.5rem;
            padding: 0;
            display: -webkit-box;
            -webkit-box-pack: center;
            -webkit-box-align: center;
            border-radius: 50%;
            border: 1px solid var(--btn-paginator-border-color);
            background-color: var(--button-bg);
            &:hover {
                background-color: var(--btn-paginator-hover-color);
            }
        }
        &.active {
            .page-link {
                background-color: var(--btn-paginator-hover-color);
                color: var(--btn-text-color);
            }
        }
        &.disabled {
            cursor: not-allowed;
            .page-link {
                color: rgba(108, 117, 125, 0.57);
                border-color: var(--btn-paginator-border-color);
                background-color: var(--button-bg);
            }
        }
        &:first-child .page-link,
        &:last-child .page-link {
            border-radius: 50%;
        }
    } // .page-item
} // .pagination
#post-list {
    margin-top: 1rem;
    padding-left: 0.5rem;
    .post-preview {
        padding-top: 1.5rem;
        padding-bottom: 1rem;
        border-bottom: 1px solid var(--main-border-color);
        a:hover {
            @extend %link-hover;
        }
        h1 {
            font-size: 1.4rem;
            margin: 0;
        }
        .post-meta {
            i {
                font-size: 0.73rem;
                &:not(:first-child) {
                    margin-right: 1.2rem;
                }
            }
            em {
                @extend %normal-font-style;
            }
        }
        .post-content {
            margin-top: 0.6rem;
            margin-bottom: 0.6rem;
            color: var(--post-list-text-color);
            >p {
                /* Make preview shorter on the homepage */
                margin: 0;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }
        }
        .pin {
            >i {
                transform: rotate(45deg);
                padding-right: 3px;
                color: var(--pin-color);
            }
            >span {
                display: none;
            }
        }
    } // .post-preview
} // #post-list

/* Hide SideBar and TOC */

@media all and (max-width: 830px) {
    .pagination {
        justify-content: space-evenly;
        .page-item {
            &:not(:first-child):not(:last-child) {
                display: none;
            }
        }
    }
}


/* Sidebar is visible */

@media all and (min-width: 831px) {
    #post-list {
        margin-top: 1.5rem;
        .post-preview .post-meta {
            .pin {
                background: var(--pin-bg);
                border-radius: 5px;
                line-height: 1.4rem;
                height: 1.3rem;
                margin-top: 3px;
                padding-right: 1px;
                padding-left: 6px;
                >span {
                    display: inline;
                }
            }
        }
    }
    .pagination {
        font-size: 0.85rem;
        .page-item {
            &:not(:last-child) {
                margin-left: 0.7rem;
            }
            .page-link {
                width: 2rem;
                height: 2rem;
            }
        }
        .page-index {
            display: none;
        }
    } // .pagination
}


/* Pannel hidden */

@media all and (max-width: 1200px) {
    #post-list {
        padding-left: 0;
    }
}
